<template>
  <b-list-group-item style='border: none'>
    <h6 class='mb-1'>
      {{data.university}}, {{data.degree}}, {{data.major}}
    </h6>
    <h6 class='mb-1'>
      From {{data.startDate}} - {{data.graduationDate}}
    </h6>
    <div class='d-flex w-100, justify-content-between'>
      <small class='text-muted'>
        <b-link @click="revealEditModal(data.educationID)">Edit</b-link>
      </small>
      <small class='text-muted'>
        <b-link @click="revealRemoveModal(data.educationID)">Remove</b-link>
      </small>
      <b-modal :id="'modal-education-edit-' + data.educationID" hide-footer>
        <b-form>
          <EducationForm :data="data"></EducationForm>
        </b-form>
      </b-modal>
      <b-modal :id="'modal-education-remove-' + data.educationID" hide-footer>
        <b-form @submit="$emit('delete-row')">
          <b-button type="submit" variant="danger">Are you sure?</b-button>
        </b-form>
      </b-modal>
    </div>
  </b-list-group-item>
</template>

<script>
/* eslint-disable */
import Vue from 'vue';
import { FormDatepickerPlugin, FormSelectPlugin, FormPlugin, FormInputPlugin, FormGroupPlugin, ModalPlugin, ListGroupPlugin, ButtonPlugin, LinkPlugin } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

const EducationForm = () => import('../form/EducationForm');

Vue.use(ListGroupPlugin);
Vue.use(FormPlugin);
Vue.use(FormInputPlugin);
Vue.use(ModalPlugin);
Vue.use(ButtonPlugin);
Vue.use(FormGroupPlugin);
Vue.use(LinkPlugin);
Vue.use(FormSelectPlugin);
Vue.use(FormDatepickerPlugin);
Vue.use(FormSelectPlugin);

export default {
  name: 'Education',
  props: [
    'data'
  ],
  components:{
    EducationForm,
  },
  methods:{
    async revealEditModal(_id){
      this.$bvModal.show('modal-education-edit-' + _id);
    },
    async revealRemoveModal(_id){
      this.$bvModal.show('modal-education-remove-' + _id);
    }
  },
}
</script>



